            display: block;
          }

          .focused{
            display: none;
          }
        }

        .field-validation-error{
          position: absolute;
          padding: 5px 15px;
          bottom: -34px;

          span{
            white-space: nowrap;
          }
        }
      }

      input{
        height: 56px;
        color: #424647;
        @include rem(16);
        border: none;
        border-bottom: 1px solid #757575;
        box-shadow: none;

        &:focus{
          outline: 0;
          border-bottom: 1px solid #26a69a;
          box-shadow: 0 1px 0 0 #26a69a;
        }
      }

      input.valid{
        border: solid 1px #007dba;
      }

      input.valid  ~ .label-field{
        @include rem(12);
        top: 0;
        color: #007dba;

        .default{
          display: none;
        }

        .focused{
          display: block;
        }
      }

      input.input-validation-error{
        border: solid 1px #ef3340;
      }

      input.input-validation-error  ~ .label-field{
        @include rem(12);
        top: 0;
        color: #ef3340;

        .default{
          display: none;
        }

        .focused{
          display: block;
        }
      }

      input:focus ~ .label-field{
        @include rem(12);
        top: 0;
        color: #007dba;

        .default{
          display: none;
        }

        .focused{
          display: block;
        }
      }
    }
  }

  .field-validation-error{
    span{
      white-space: nowrap;
    }
  }

  .umbraco-forms-navigation{
    button{
      margin: auto;
      display: table;
      width: 162px;
      min-height: 48px;
      padding: 12px 30px 12px 40px;
      text-align: left;
      border: solid 1px #424647;
      background-color: #fafafa;
      color: $black;
    }
  }

  * + .umbraco-forms-navigation{
    margin-top: 30px;
  }
}

