.megamenu{
  .faceted-filter-block{
    background-color: #1c2240;

    .section-padding{
      background-color: #1c2240;

      .filters-buttons{
        .show-filters-text,
        .hide-filters-text{
          color: $white;
        }
      }
    }

    .facets-results{
      background-color: #f4f4f4;
    }

    .limiter-1220{
      padding-bottom: 26px;
      padding-right: 15px;
      padding-left: 15px;
      padding-bottom: 130px;
    }

    form{
      max-width: 345px;
      margin: 0 auto;
    }
  }
}

.faceted-filter-block{
  &[data-document-type="all_documents"]{
      .facets-results{
      .pagination-block{
        margin-bottom: 0;
      }
    }
  }

  .section-padding{
    padding: 40px 0;
  }

  .limiter{
    margin: 0 auto;
    max-width: 770px;

    .form{
      .col-12{
        padding-right: 50px;
        padding-left: 50px;
      }
    }
  }

  #search-facets,
  .global-search{
    input{
      padding: 14px;
      border-radius: 6px;
      border: solid 1px #757575;
      background-color: #ffffff;
      height: auto;
      width: 100%;
      margin: 0 0 5px 0;
    }

    .submit-button{
      right: 25px;
      top: 50%;
      @include transform(translateY(-50%));
      padding: 0;
      margin: 0;
      border: none;
      background-color: $white;
      width: 24px;
      height: 29px;
    }
  }

  .title + .form{
    margin-top: 20px;
  }

  * + .filters-buttons{
    margin-top: 25px;
  }

  .switch-filters-block{
    .filtered-text-switcher{
      .hide-filters-text{
        display: none;
      }
    }

    .container-switcher-button{
      min-height: 40px;

      .open-filters{
        width: 40px;
        height: 40px;
        padding: 0;
        margin: 0;
        border: none;
        border-radius: 50%;
        background-color: #ffffff;
        @include transform(rotate(0deg));

        span{
          left: 50%;
          top: 50%;
          @include transform(translate(-50%, -50%));
          color: #424647;
          width: 25px;
          height: 20px;
        }
      }
    }

    .filtered-text-switcher + .container-switcher-button{
      margin-top: 8px;
    }

    &.opened{
      .filtered-text-switcher{
        .hide-filters-text{
          display: block;
        }

        .show-filters-text{
          display: none;
        }
      }

      .container-switcher-button{
        .open-filters{
          @include transform(rotate(180deg));
        }
      }
    }
  }

  .filters-section{
    .tab-content{
      .tab-pane{
        .container-dates{
          .label-dates{
            color: $white;
          }
        }
      }
    }

    .limiter-1210{
      padding-right: 18px;
      padding-left: 18px;
    }

    .filter-actions{
      .clear-filters{
        line-height: 51px;
        display: inline-block;
      }

      .clear-filters + .apply-filters{
        margin-left: 30px;
      }
    }

    &.has-filters{
      .filter-actions{
        .clear-filters{
          display: inline-block;
        }
      }
    }

    .nav{
      display: -webkit-box;
      flex-wrap: unset;
      width: 100%;
      overflow-x: scroll;
      padding-left: 20px;
      padding-right: 20px;

      .nav-item{
        .nav-link{
          margin-bottom: 0 !important;
          border: 2px solid transparent !important;
          border-top-left-radius: 0 !important;
          border-top-right-radius: 0 !important;
          padding: 0 0 15px 0;

          &.active{
            border-bottom: 2px solid $orange-bright !important;
          }

          &:first-child{
            margin-left: 15px;
          }

          &:last-child{
            margin-right: 15px;
          }
        }

        & + .nav-item{
          margin-left: 60px;
        }
      }
    }

    /* Hide scrollbar for Chrome, Safari and Opera */
    .nav::-webkit-scrollbar{
      display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox */
    .nav{
      -ms-overflow-style: none;  /* IE and Edge */
      scrollbar-width: none;  /* Firefox */
    }

    .tab-content{
      .tab-pane{
        padding-top: 40px;

        .filter-item{
          .filter-button{
            padding: 7px 20px;
            border-radius: 21.5px;
            border: solid 1px #ffffff;
            background-color: #13172c;
            color: #ffffff;

            .filter-name{
              display: block;
              margin: 0;
              padding: 0;
              text-align: left;
              text-transform: capitalize;
            }

            .check-icon{
              display: none;
              right: 25px;
              top: 50%;
              @include transform(translateY(-50%));
            }
          }

          &.active{
            .filter-button{
              border: solid 1px #ffffff;
              background-color: #ffffff;

              .filter-name{
                color: #000000;
              }

              .check-icon{
                display: block;
                color: #000000;
              }
            }
          }

          & + .filter-item {
            margin-top: 20px;
          }
        }
      }
    }

    .filter-actions{
      margin-top: 40px;
    }
  }

  * + .filters-section{
    margin-top: 40px;
  }

  .facets-results{
    &.container-fluid{
      padding: 70px 15px;
    }

    .row{
      margin: 0 -15px;
    }

    .choose-view{
      .btn-group{
        button{
          color: #424647;
          background-color: #ececec;
          border-color: #c1c1c1;

          &:before {
            font-family: $awesome-icons-font;
          }

          &.active{
            background-color: #c1c1c1;
            border-color: #c1c1c1;
          }

          &:focus{
            box-shadow: none;
          }
        }
      }
    }

    .result-item{
      margin-top: 40px;
      padding: 0 15px;

      .container-card{
        border-top: 3px solid #ba4d01;
        padding: 40px 30px 86px;
        height: 100%;

        .categories{
          .title-container{
            color: #ba4d01;
            @include rem-2(16, 24);
            font-weight: 500;
            margin: 0;
            overflow: hidden;

            span.material-icons{
              @include rem-2(7, 24);
            }
          }

          .card-option{
            margin: 0;
            @include rem-2(16, 24);
            color: #424647;
            font-weight: 500;
            position: absolute;
            right: 0;
            top: -27px;
            left: 0;
            text-align: right;
            overflow: hidden;
            background-color: #f4f4f4;
          }
        }

        picture{
          position: absolute;
          height: 100%;
          width: 100%;
          top: 0;
          overflow: hidden;
          left: 0;

          img{
            top: 0;
            right: 0;
            bottom: 0;
            left: 50%;
            width: auto;
            height: 100%;
            @include transform(translateX(-50%));
          }
        }

        .title{
          overflow: hidden;
          @include rem-2(20, 24);
          color: #424647;
          margin: 0;
        }

        * + .title{
          margin-top: 20px;
        }

        .date{
          @include rem-2(14, 24);
          color: #424647;
          margin: 0;
          overflow: hidden;
        }

        * + .date{
          margin-top: 10px;
        }

        .description{
          overflow: hidden;
          color: #424647;
        }

        * + .description{
          margin-top: 10px;
        }

        .cta{
          position: absolute;
          bottom: 40px;
          left: 0;
          right: 0;
          padding: 0 30px;

          a{
            color: #424647;
          }
        }
      }

      button.download-button{
        position: relative;
        text-align: left;
        width: 100%;
        border-bottom: 1px solid #000;
        padding: 20px 50px 20px 30px;
      }

      & + .result-item{
        margin-top: 40px;
      }
    }

    .pagination-block{
      margin-right: 45px;
      margin-left: 45px;
    }

    * + .pagination-block{
      margin-top: 50px;
    }

    &.grids{
      .results{
        .result-item{
          &.has-image{
            .categories{
              .title-container{
                position: relative;
                z-index: 3;
                color: #ffa860;
              }
            }

            .date{
              position: relative;
              z-index: 3;
              color: #ffffff;
            }

            .description{
              position: relative;
              z-index: 3;
              color: #ffffff;
            }
          }
        }
      }
    }
  }

  &[data-document-type="press_releases"],
  &[data-document-type="newsletters"],
  &[data-document-type="news"],
  &[data-document-type="fact_sheets_publications"]{
    .facets-results{
      .result-item{
        .container-card{
          .categories{
            .title-container{
              font-size: 16px;
              font-weight: 500;
              line-height: 24px;
              color: #424647;
            }
          }

          .date{
            color: #ba4d01;
          }
        }
      }
    }
  }
}

.megamenu{
  .faceted-filter-block{
    &[data-document-type="all_documents"]{
      .facets-results{
        .results{
          .result-item{
            .container-card{
