.faceted-filter-block {
  .section-padding {
    .tab-container{
      display: none;

      &.active{
        display: block;
      }
    }

    .limiter {
      .container-fluid {
        .form-group {
          margin: 0 auto;
          max-width: 540px;

          &.search-form{
            padding: 0 25px;
          }


          .input-field {
            margin: 20px auto;
            width: 100% !important;

            .input-group-prepend {
              border: none;
              background-color: transparent;

              .input-group-text {
                position: absolute;
                top: 25%;
                padding-top: 3px;
                right: 0;
                background: none;
                border: none;

                span {
                  display: none;
                }
              }
            }

            &.input-outlined {
              > input {
                @include border-radius(6px);
                margin: 0;
              }
            }

            &::before {
              content: "\e8b6";
              font-family: $material-icons;
              @include rem(26);
              color: $gray-901;
              display: block;
              width: 26px;
              height: 26px;
              position: absolute;
              right: 15px;
              line-height: 100%;
              top: 50%;
              @include transform(translateY(-50%));
            }
          }
        }

        .faceted-hide-filter {
          text-align: center;
          color: $white;
          background-color: transparent;
          display: block;
          margin: 0 auto;
          outline: none;
          border: none;

          p {
            margin-bottom: 8px;
            overflow-wrap: break-word;
          }

          .text-1 {
            display: none;
          }

          &.active {
            .text-1 {
              display: block;
            }

            .text-2 {
              display: none;
            }

            span.material-icons {
              @include transform (rotate(180deg));
            }
          }

          .icon-faceted-filter {
            width: 40px;
            height: 40px;
            @include border-radius(50px);
            color: $gray-900;
            background-color: $white;
            padding: 8px 7px 7px 8px;
            cursor: pointer;
          }
        }
      }
    }
  }

  &.results{
    .pagination-buttons-container {
      margin-top: 70px;
      display: flex;
      justify-content: center;

      .link-default {
        background-color: transparent;
        color: $gray-900;

        .material-icons {
          vertical-align: middle;
        }

        &.jplist-disabled {
          color: rgba(66, 70, 71, 0.2);
        }
      }

      .jplist-holder {
        display: flex;
        justify-content: space-around;

        button {
          padding: 0 7px;//here you can change the space between each pagination button.

          &.jplist-selected {
            color: $color-text-dark-orange;
            border-bottom: 1px solid $color-text-dark-orange;
          }
        }
      }
    }
  }

  .card-view{
    .card-container{
      .card {
        border: none;
      }

      .card-title{
        margin-bottom: 0;

        .title.h3 {
          margin-bottom: 22px;
        }
      }

      .card-content{
        border: none;

        .card-body{
          border-bottom: 1px solid $gray-450;

          .card-download {
            .material-download {
              font-family: "Material Icons",sans-serif;
              font-style: normal;
              width: 25px;
              font-size: 25px;

              &::before {
                content: '\e884';
              }
            }
          }
        }
      }

      & + .section-letter {
        margin-top: 65px;
      }
    }
  }

  .tab-container {
    .tabs-wrapper {
      .nav-tabs {
        display: -webkit-box;
        flex-wrap: unset;
        width: 100%;
        overflow: scroll;
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */
        padding-left: 20px;

        &::-webkit-scrollbar {
          display: none;
        }

        .nav-item {
          .nav-link {
            color: $white;
            font-weight: 500;
            padding-right: 0;
            padding-left: 0;
            font-size: 20px;
            line-height: 1.35;
            padding-bottom: 15px;
            letter-spacing: 0.15px;
            border: none;
            border-bottom: 2px solid transparent;

            &.active {
              background-color: transparent;
              border: none;
              color: $orange-bright;
              border-bottom: 2px solid $orange-bright;
            }

            &:focus{
              color: #ffa860;
              outline: none;
              border: none;
              border-bottom: 2px solid #ffa860;
            }

            &:hover{
              color: #ffa860;
              outline: none;
              border: none;
              border-bottom: 2px solid #ffa860;
            }
          }

          & + .nav-item {
            margin-left: 60px !important;
          }

          &:last-child{
            padding-right: 20px;
          }
        }
      }

      .apply-search {
        a {
          color: $blue-light;
        }

        button.link-default {
          color: $blue-light;
          background-color: transparent;
          margin-right: 20px;
        }

        .btn {
          padding: 9px 65px 8px 40px;

          &::after {
            position: absolute;
            display: block;
            top: 50%;
            right: 35px;
            @include transform(translateY(-50%));
            font-family: 'IcoFont',sans-serif;
            font-size: 1.25rem;
            content: "\ea5d";
          }
        }

        a + .btn {
          margin-left: 30px;
        }

        a.d-none + .btn {
          margin-left: 0;
        }
      }

      .tab-content {
        background-color: $blue-dark;
        padding-top: 21px;
        padding-bottom: 39px;

        .tab-pane {
          .container-fluid {
            overflow: scroll;
            max-height: 232px;
            -ms-overflow-style: none; /* IE and Edge */
            scrollbar-width: none; /* Firefox */

            &::-webkit-scrollbar {
              display: none
            }

            .radio-group {
              margin: 0 auto;
              max-width: 275px;

              label {
                width: fit-content;
                float: left;
                font-size: 16px;
                font-weight: 500;
                line-height: 1.25;
                color: $white;
              }

              .btn {
                background-color: transparent;
                border: 1px solid $white;
                @include border-radius(21.5px);
                max-width: 289px;
                margin-top: 20px;

                input {
                  position: relative;
                  width: 0;
                }

                .input-group-prepend {
                  .input-group-text {
                    position: absolute;
                    top: 50%;
                    right: 0;
                    background: none;
                    border: none;
                    @include transform(translateY(-50%));

                    span {
                      @include rem(19);
                      background-color: $white;
                      @include border-radius(50px);
                      font-weight: 600;
                      padding-top: 2px;
                      height: 19px;
                      width: 19px;
                      font-size: 15px;
                      color: $black;
                    }
                  }
                }

                &.active {
                  background-color: $white;
                  color: $gray-900;

                  .input-group-prepend {
                    .input-group-text {
                      span {
                        background-color: $gray-900;
                        color: $white;
                      }
                    }
                  }
                }
              }
            }

            .radio-group.date {
              padding-top: 30px;
              margin: 0;
            }

            &.split-fields {
              align-items: initial;

              .form-elements-styles-block {
                padding-top: 30px;
                padding-left: 60px;

                form {
                  margin-left: 100px;
                }

                .field-group-date {
                  width: fit-content;
                  font-family: $font-family-sans-serif;
                  font-size: 16px;
                  font-weight: 500;
                  line-height: 1.25;
                  margin-bottom: 20px;

                  label {
                    color: $white;
                    margin: 0;
                  }
                }

                .date-input {
                  display: inline-block;

                  .date-input-label {
                    margin-bottom: 5px;
                    width: fit-content;
                    font-family: $font-family-sans-serif;
                    font-size: 14px;
                    line-height: 1.14;
                    color: rgba(255, 255, 255, 0.9);
                  }

                  .input-field {
                    margin-top: 0;
                    width: 104px;

                    .datepicker.validate.valid {
                      font-family: $font-family-sans-serif;
                      font-size: 16px;
                      line-height: 1.5;
                      color: $gray-900;
                    }

                    label {
                      font-size: 12px;
                      line-height: 1.33;
                      color: $color-text-dark-orange;
                    }
                  }
                }

                & + .radio-group {
                  .group-title {
                    width: 100%;
                  }

                  .btn {
                    height: 43px;
                    padding-top: 9px;

                    .input-group-text {
                      padding: 11px 20px;
                    }

                    & + .btn {
                      margin-left: 0;
                    }
                  }
                }
              }
            }
          }
        }

        .tab-pane + .apply-search {
          margin: 45px;
        }
      }
    }
  }
}

