              }

              .sup-title {
                font-size: 20px;
                font-weight: 500;
                line-height: 1.2rem;
                letter-spacing: 0.15px;
              }

              .content-btn-download{
                bottom: 30px;
              }

              * + .sub-title {
                margin-top: 5px;
              }

              .text-body {
                font-size: 16px;
                font-weight: 500;
                line-height: 1.5rem;
              }

              * + .text-body {
                margin-top: 10px;
              }
            }
          }
        }
      }
    }
  }
}

